import { GET_CONTACT, ADD_CONTACT, EDIT_CONTACT, DEL_CONTACT } from "../action/type";


// initialState  Store  Redux Reducer 
const initialState = {

    // StoreData  store 
    // storeA  StoreData()   storeB  storeA1
    // storeA  Data  contact

    storeA: [

    ],
    storeB:[

    ],
}

//  initialState   state  Store  Redux Reducer  config  reducers/index
// actin  Object  Component 
export default (state = initialState, action) => {
  switch (action.type) {
      case GET_CONTACT:
        return {
          ...state,
          storeA: action.payload
        };

      case ADD_CONTACT:
        return {
          ...state,
          storeA: [action.payload, ...state.storeA],
      };

      case EDIT_CONTACT:
        return {
          ...state,
          storeA: state.storeA == action.payload,
      };

      case DEL_CONTACT:
        return {
          ...state,
          storeA: state.storeA.filter(contact => // stroeA =  stroeA  () payload.id 
          contact.id !== action.payload),  //payload  id
        };  

      default:
        return state
      }
}



