import React, { Component } from 'react'
import Contact from './Contact';
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import { getContact } from '../../action/Action';




class Contacts extends Component {
    componentDidMount() {
        this.props.getContact();
    }
    

    render() {
        //Map state  this.props.dataContact
        const { dataContacts } = this.props;
        return (
            <React.Fragment>
                <h1 className="dispaly-4 mb-2">
                    <span className="text-danger">Contact</span>List SecWind
                </h1>
                {/* Map dataContacts  dataContact  Contact.js */}
                { dataContacts.map(dataContact => (  
                    <Contact
                        key= {dataContact.id}
                        dataContact= {dataContact}
                    />
                ))} 
            </React.Fragment>          
        ) 
     
    }
}

Contacts.propTypes = {
    dataContacts: PropTypes.array.isRequired,
    getContact: PropTypes.func.isRequired,
}

const mapStateToProps = (state) => ({
    dataContacts: state.StoreData.storeA
})



export default connect(
    mapStateToProps,
    {getContact},
    )(Contacts);
    
        

//  Render  componentDidMount  func this.props.getContact() 
// getContact  action  get  StoreA  Type  GET_CONTACT  Return state
